.write-comments-container {
  display: flex;
  justify-content: center;
  padding: 2% 20%;
  background: #ececec;
}

.write-comment-submit-btn button {
  color: #fff;
  background-color: #6648cd;
  font-weight: 600;
  border: none;
  padding: 8px 14px;
  border-radius: 25px;
  height: 52px;
  width: 130px;
}

.write-comment-avatar {
  display: flex;
  align-items: center;
}

.write-comment-text {
  display: flex;
  align-items: center;
  padding: 0 25px 0 5px;
}

.write-comment-text input {
  background-color: #fff;
  font-weight: 600;
  border: none;
  padding: 8px 14px;
  border-radius: 25px;
  height: 35px;
  width: 500px;
  &:focus{
    outline:none
  }
}

.write-comment-submit-btn button:hover{
    cursor: pointer;
}
