.tab-container {
  display: flex;
  padding: 1% 8% 0;
}
button.MuiButtonBase-root.MuiTab-root.MuiTab-textColorPrimary.tab-item {
  min-width: 100px;
  padding: 6px 8px;
  font-size: 0.775rem;
  font-weight: 600;
  letter-spacing: 0em;
}

span.delimeter {
  padding: 0% 1%;
  margin-right: 80px;
}

.tab-menu {
  width: 80%;
}

.tab-button {
  width: 20%;
  margin: 8px;
  border-left: 1px solid;
  color: #8080804a;
}

.explore-button {
  color: #6648cd;
  background-color: #efecfa;
  font-weight: 600;
  letter-spacing: 0;
  border: none;
  padding: 8px 14px;
  border-radius: 4px;
  margin-top: -2px;
}

@media only screen and (max-width: 980px) {
  .tab-container {
    padding: 1% 1% 0;
  }
  .button.MuiButtonBase-root.MuiTab-root.MuiTab-textColorPrimary.tab-item {
    min-width: 85px;
  }
}
