.comments-header {
  display: flex;
  padding: 20px 10px;
  background: #8c2ed4;
}

.comment-counter {
  flex: 1;
  font-weight: 600;
  color: #fff;
  .counter-value {
    padding: 0 100px;
  }
}

.comments-buttons {
  flex: 1;
  display: flex;
  justify-content: center;
  .comment-button {
    border: none;
    background: inherit;
    color: #fff;
    margin: 0 10px;
    /* text-transform: uppercase; */
  }
}
.comments-body {
  padding: 10px 150px;
  background: #ececec;

  .comment-card {
    background: #fff;
    padding: 10px 40px;
    display: flex;
    .comment-avatar {
      padding: 10px;
    }
    .comment {
      padding: 10px;

      .comment-author {
        font-weight: 600;
      }

      .comment-subject {
        padding: 8px 0;
      }

      .comment-details {
        display: flex;

        .like-container {
          display: flex;
          align-items: center;
        }
        .like-count {
          margin: 0 4px;
        }

        .duration-container {
          display: flex;
          align-items: center;
          margin: 0 20px;
        }
        .duration {
          margin: 0 4px;
        }
      }
    }
  }
}
