* {
  font-family: sans-serif;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.paragraph {
  padding: 100px 259px;
  line-height: 38px;
  color: #545454;
}

.split-container{
  display: flex;
}

.left-section, .right-section{
  flex: 1
}

.text-section{
  display: flex;
  align-items: center;
}

.text-quotes {
  padding: 0 150px;
  font-size: 1.20rem;
  line-height: 40px;
  color: #6648cd;
}

.author-name {
  padding-top: 30px;
  font-weight: bold;
}

.double-quotes svg {
  transform: rotate(180deg);
  margin-left: -5px;
}

.black-color{
  color: #000000d4
}

@media only screen and (max-width: 980px) {
  .text-quotes{
    padding: 45px;
    font-size: 1rem;
  }
}